@import url(https://fonts.googleapis.com/css?family=Work+Sans:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {font-size: 100%;} /*16px*/

body {
  font-family: 'Work Sans', sans-serif;
  font-weight: 700;
  line-height: 1.75;
}

p {
  margin: 0;
}

h1, h2, h3, h4, h5 {
  margin: 3rem 0 1.38rem;
  font-family: 'Work Sans', sans-serif;
  line-height: 1.3;
  color: hsl(0, 0%, 99%);
}

h1 {
  margin: 0;
  font-size: 2.488rem;
}

h2 {font-size: 2.074rem;}

h3 {font-size: 1.728rem;}

h4 {font-size: 1.44rem;}

h5 {font-size: 1.2rem;}

small, .text_small {font-size: 0.833rem;}

a {
  color: hsl(291, 47%, 70%);
  text-decoration: none;
  transition: 0.3s;
}

a:hover {
  color: hsl(291, 47%, 90%);
}

.app {
  padding: 3rem 1rem 1rem;
  min-height: 100vh;
  background: hsl(300, 5%, 10%);
  color: hsl(0, 0%, 70%);
}

.container {
  margin: auto;
  width: 100%;
  max-width: 1000px;
}

main {
  text-align: center;
  display: grid;
}

@media (min-width: 768px) {
  main {
    grid-template-columns: auto auto;
  }
}
.header {
    text-align: center;
}

.title {
    line-height: 1.2;
}

.subtitle {
    margin: 1rem auto 2rem;
    line-height: 1.2;
}

.color-window {
    margin: auto auto 1rem;
    height: 200px;
    width: 100%;
}

.text-block {
    margin: 2rem auto;
    display: inline-grid;
    grid-template-columns: -webkit-min-content -webkit-min-content;
    grid-template-columns: min-content min-content;
    grid-gap: 1rem;
    gap: 1rem;
}

.text-block p span {
    margin: 0;
    text-align: center;
    white-space: nowrap;
    transition: 0.3s;
}

.text-block p span:hover {
    cursor: pointer;
    color: hsl(0, 0%, 99%);
}
.footer {
    text-align: center;
}
.slider-group {
    display: flex;
    justify-content: center;
}

.slider-label {
  font-weight: 700;
  width: 75px;
  text-align: left;
}

.slider-label ~ .slider-label {
  margin-top: 3rem;
}

.slider-input {
  margin: 0;
  flex: 1 1;
  max-width: 360px;
}
.title {
  text-align: center;
}

.color-grid {
    margin: auto;
    display: inline-grid;
    grid-template-columns: auto;
}

.color-grid-cell {
    margin-bottom: 3rem;
    display: inline-grid;
    grid-template-columns: -webkit-min-content -webkit-min-content;
    grid-template-columns: min-content min-content;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
}

.color-grid-text-block {
    display: grid;
    grid-template-columns: 1fr -webkit-min-content;
    grid-template-columns: 1fr min-content;
    grid-gap: 1rem;
    gap: 1rem;
    text-align: left;
}

.color-grid-text-block p span{
    cursor: pointer;
    white-space: nowrap;
    transition: 0.3s;
}

.color-grid-text-block p span:hover {
    color: hsl(0, 0%, 99%);
}

.color-grid-swatch {
    width: 125px;
    height: 125px;
    place-self: center;
}

@media (max-width: 375px) {
    .color-grid-swatch {
        width: 90px;
        height: 90px;
    }
}
